<!--
 * @Description: 排行榜
 * @Author: fjt
 * @Date: 2021-07-06 17:32:10
 * @LastEditTime: 2021-07-06 11:09:20
 * @LastEditors: fjt
-->
<script>
import mobileHead from './images/mobile-head.png';
import { getPromoterRank, savePromoterRank } from './api';
import mobileTimeSwitch from './images/mobile-time-switch.png';
import mobileProfilePicture from './images/mobile-profile-picture.png';
const bg_url = 'https://qmxq.oss-cn-hangzhou.aliyuncs.com/promoter-ranking-list.png';

export default {
  name: 'leaderboard',
  data() {
    const integerRex = /^[1-9]\d*$/;
    return {
      loading: true,
      disabled: true,
      form: {
        enable: 1, //0关闭  1开启
        bg_url: '', //  背景图
        ranking_num: '',
        ranking_dimension: [] // 排名维度  ["total_bonus", "all_children", "total_money"]
      },
      rules: {
        ranking_num: [
          {
            required: true,
            validator: (rule, value, callback) => {
              const errStr = '请输入1~100之间的整数';

              if (!integerRex.test(value)) {
                callback(errStr);
                return;
              }
              if (this.$_.toSafeInteger(value) > 100) {
                callback(errStr);
              }
              callback();
            }
          }
        ],
        ranking_dimension: [
          {
            required: true,
            message: '请至少选择一个排名维度'
          }
        ]
      }
    };
  },
  methods: {
    submit() {
      this.$refs['form'].validate(valid => {
        if (valid) {
          this.loading = true;
          savePromoterRank(this.form)
            .then(() => {
              this.loading = false;
            })
            .catch(() => {
              this.loading = false;
            });
        } else {
          return false;
        }
      });
    }
  },
  mounted() {
    getPromoterRank()
      .then(response => {
        if (!this.$_.isEmpty(response)) {
          this.form = response.content;
        } else {
          this.form.bg_url = bg_url;
        }
        this.loading = false;
        this.disabled = false;
      })
      .catch(() => {
        this.loading = false;
        this.disabled = false;
      });
  },
  render() {
    const self = this;
    const dimension = [
      {
        label: 'total_bonus',
        name: '累计佣金'
      },
      {
        label: 'all_children',
        name: '当前下线'
      },
      {
        label: 'total_money',
        name: '累计销售金额'
      }
    ];
    console.log('修改');
    let users = [];
    let userNum = this.$_.toSafeInteger(this.form.ranking_num);
    users = Array.from({ length: userNum });
    return (
      <div class="le-main">
        <el-form
          class="flex"
          label-width="124px"
          ref="form"
          props={{ model: self.form, rules: self.rules }}
          v-loading={self.loading}>
          <div class="le-card le-left">
            <div class="le-left--content">
              <div class="le-text">动态</div>
              <img src={mobileHead} alt="" width="338" height="80" />
              {(() => {
                if (self.form.enable === 0) {
                  return (
                    <div class="le-nothing flex align-center justify-center">
                      <span>未启用</span>
                    </div>
                  );
                } else {
                  return (
                    <div class="le-left--list">
                      <img src={self.form.bg_url} width="338" height="180" alt="" />
                      <div
                        class="le-content"
                        class={['le-content', self.$_.isEmpty(self.form.ranking_dimension) ? '' : 'le-padding']}>
                        {(() => {
                          if (self.$_.isEmpty(self.form.ranking_dimension)) {
                            return <div class="le-noting">请选择排名维度</div>;
                          } else {
                            return [
                              [<img src={mobileTimeSwitch} alt="" />],
                              [
                                <div class="le-my--rank flex align-center">
                                  <div class="le-noun">
                                    我的
                                    <br />
                                    排名
                                  </div>
                                  <img
                                    class="le-profile--picture"
                                    src={mobileProfilePicture}
                                    width="36"
                                    height="36"
                                    alt=""
                                  />
                                  <div>
                                    <div class="le-user--name">用户4</div>
                                    <div class="le-user-aid">累计佣金:￥10.00 排名:4</div>
                                  </div>
                                </div>
                              ],
                              [
                                <table class="le-table">
                                  <thead>
                                    <tr>
                                      <th>排名</th>
                                      <th>用户</th>
                                      <th>累计佣金</th>
                                    </tr>
                                  </thead>
                                  <tbody>
                                    {users.map((item, index) => {
                                      return (
                                        <tr key={index} class="le-table--item">
                                          <td>1</td>
                                          <td>
                                            <img
                                              class="le-profile--picture"
                                              src={mobileProfilePicture}
                                              width="36"
                                              height="36"
                                              alt=""
                                            />
                                          </td>
                                          <td>￥25.00</td>
                                        </tr>
                                      );
                                    })}
                                  </tbody>
                                </table>
                              ]
                            ];
                          }
                        })()}
                      </div>
                    </div>
                  );
                }
              })()}
            </div>
          </div>
          <div class="le-card flex-sub le-right">
            <div class="le-card-head flex align-center">
              <span class="le-card-line" />
              <span>编辑排行榜</span>
            </div>
            <el-form-item label="是否启用排行榜" prop="name">
              <el-radio-group v-model={self.form.enable}>
                <el-radio label={1}>启用</el-radio>
                <el-radio label={0}>禁用</el-radio>
              </el-radio-group>
              <div class="le-prompt-text">启用后，将在分销中心显示排行榜的入口</div>
            </el-form-item>
            {(() => {
              if (self.form.enable === 1) {
                return [
                  [
                    <el-form-item label="背景图">
                      <div class="select-cover__120">
                        <pictureDialog
                          v-model={self.form.bg_url}
                          limit={1}
                          scopedSlots={{
                            upload() {
                              return (
                                <div class="select-cover__120-add">
                                  <i class="le-icon le-icon-add select-cover__120-icon" />
                                  <span class="select-cover__120-text">添加图片</span>
                                </div>
                              );
                            },
                            preview(scope) {
                              return (
                                <div class="select-cover__120-edit">
                                  <el-image src={scope.url} fit="cover" />
                                  <div class="select-cover__120-tips">
                                    <span>替换</span>
                                  </div>
                                </div>
                              );
                            }
                          }}
                        />
                      </div>
                      <div class="he-tips">
                        <el-button type="text" onClick={() => (self.form.bg_url = bg_url)} class="le-reset">
                          重置为默认
                        </el-button>
                        <span class="le-prompt-text">建议尺寸：750像素 * 400像素</span>
                      </div>
                    </el-form-item>
                  ],
                  [
                    <el-form-item label="排名数" prop="ranking_num">
                      <el-input class="le-input--240" v-model={self.form.ranking_num} placeholder="请输入排名数" />
                    </el-form-item>
                  ],
                  [
                    <el-form-item label="排名维度" prop="ranking_dimension">
                      <el-checkbox-group v-model={self.form.ranking_dimension}>
                        {dimension.map((item, index) => {
                          return (
                            <el-checkbox label={item.label} key={index}>
                              {item.name}
                            </el-checkbox>
                          );
                        })}
                      </el-checkbox-group>
                    </el-form-item>
                  ]
                ];
              }
            })()}
          </div>
        </el-form>
        <div class="le-cardpin">
          <el-button
            type="primary"
            loading={self.loading && !self.disabled}
            disabled={self.disabled}
            onClick={self.submit}>
            保存
          </el-button>
        </div>
      </div>
    );
  }
};
</script>

<style scoped lang="less">
@import './index.less';
img {
  display: block;
}
.le-left {
  width: 380px;
  border: 1px solid #dcdfe6;
  border-radius: 40px;
  margin-right: 24px;
  padding: 37px 20px;
  min-height: 679px;
}
.le-left--content {
  border: 1px solid #dcdfe6;
  height: 100%;
  overflow: hidden;
  position: relative;
  background: #f5f5f5;
  .le-text:extend(.font-family-yahei) {
    font-size: 16px;
    font-weight: bold;
    color: #262626;
    text-align: center;
    position: absolute;
    width: 100%;
    top: 50px;
  }
  .le-left--list {
    width: 100%;
    height: calc(100% - 80px);
    border-radius: 16px 16px 0 0;
    position: relative;
    z-index: 1;
    .le-content {
      width: 338px;
      height: 383px;
      background: #ffffff;
      border-radius: 16px 16px 0 0;
      margin-top: -40px;
      z-index: 1;
      position: relative;
      overflow: hidden;
      padding: 14px;
      .le-noting:extend(.font-family-yahei) {
        font-size: 14px;
        font-weight: 400;
        color: #595959;
        line-height: 44px;
        text-align: center;
        margin-top: 26px;
      }
      .le-my--rank {
        height: 64px;
        background: #ffffff;
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.06);
        border-radius: 8px;
        margin: 18px 0 20px 0;
        padding: 14px;
      }
      .le-profile--picture {
        margin: 0 14px 0 10px;
      }
      .le-noun:extend(.font-family-yahei) {
        width: 24px;
        height: 26px;
        font-size: 12px;
        font-weight: 400;
        color: #999999;
        line-height: 14px;
      }
      .le-user--name:extend(.font-family-yahei) {
        font-size: 12px;
        font-weight: 400;
        color: #222222;
        line-height: 22px;
      }
      .le-user-aid:extend(.font-family-yahei) {
        font-size: 10px;
        font-weight: 400;
        color: #999999;
        line-height: 14px;
      }
      .le-table {
        width: 100%;
      }
      .le-table--item {
        padding-bottom: 15px;
        border-spacing: 20px 20px;
      }
    }
  }
  .le-nothing:extend(.font-family-yahei) {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    font-size: 14px;
    font-weight: 400;
    color: #ffffff;
  }
}
.le-card-head {
  margin-bottom: 24px;
}
.le-right {
  height: 450px;
  .le-reset {
    padding: 0 8px 0 0;
  }
  .select-cover__120 {
    border-radius: 4px;
    overflow: hidden;
  }
}
</style>
